<template>
  <div class="wraper" >
    <div class="hello">
      <mt-header title="新闻详情" id="header">
        <router-link to="/" slot="left" >
          <mt-button icon="back"></mt-button>
        </router-link>
      </mt-header>
    </div>
    <div class="wrap" id="changeimg">
      <h2 v-text="item.title">习近平访美</h2>
      <p v-html="item.content">在中国，国家主席访美前夕</p>
      <span v-text="new Date(item.createTime).getFullYear()+'年'+(new Date(item.createTime).getMonth()+1)+'月'+new Date(item.createTime).getDate()+'日'">2015年09月11日 15：50</span>
    </div>
  </div>
</template>

<script>
  import  axios from 'axios'
    export default {
        name: "detailnews",
        data(){
          return{
            item : {}
          }
        },
        methods:{
          getdata(){
            let that = this;
            axios.get('/api/news/get?id='+this.$route.query.id)
              .then(function (response) {
                that.item = response.data.data[0]
              })
              .catch(function (error) {
                console.log(error);
              });
          }
        },
      mounted() {
        this.getdata()
      }
    }
</script>

<style>
  #changeimg p img{
    display: block;
    width: 320px;
    height: 200px;
  }
</style>

<style scoped>
.wraper .wrap{
  width: 320px;
  margin:50px auto 50px;
  overflow: hidden;
}

.wraper span{
  float: right;
}
.wraper  .hello{
  position: fixed;
  top: 0;
  left:0;
  width: 100%;
}
</style>



